.about__container {
  padding: 30px 7%;
  margin-right: auto;
  margin-left: auto;
  background-color: #f7e5dc;
  .about__row {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
    flex: 0 0 100%;
    flex-wrap: wrap;
    padding-top: 50px;
    padding-bottom: 50px;
    .about__img-wrapper {
      height: auto;
      flex-basis: 50%;
      max-width: 50%;
      padding: 0 50px 0 30px;
      text-align: center;
      .about__img {
        max-width: 90%;
        max-height: 90%;
        border-radius: 10px;
      }
    }
    .about__info {
      flex-basis: 50%;
      max-width: 50%;
      text-align: center;
      padding: 0 50px 0 30px;
      .about__title {
        font-family: "playball", "cursive";
        font-size: 6rem;
        color: #b9702c;
      }
      .about__subtitle {
        position: relative;
        font-size: 1.5rem;
        margin-bottom: 30px;
        &:after {
          content: "";
          position: absolute;
          width: 130px;
          height: 1px;
          background: black;
          bottom: 0;
          top: 65px;
          left: 0;
          right: 0;
          margin: auto;
        }
      }

      .about__description {
        margin-top: 40px;
      }
    }
  }
}

@media (max-width: 1000px) {
  .about__container {
    height: 100vh;
    .about__row {
      .about__img-wrapper {
        padding: 0;
        flex-basis: 100%;
        max-width: 100%;
        .about__img {
          padding: 20px 0;
        }
      }
      flex-direction: column;
      .about__info {
        flex-basis: 100%;
        max-width: 100%;
        padding: 30px 0;
        .about__title {
          font-size: 4.3rem;
        }
        .about__subtitle {
          position: relative;
          font-size: 1rem;
        }
      }
    }
  }
}
